import React from 'react';
import { Footer } from '../components';
import blogs from '../blogs.json';
import { Link } from 'react-router-dom';

const Cart = ({ itemsInCart, onCartItemRemove }) => {
  const countPrice = (size) => {
    if (size === 'a3') {
      return 450;
    } else if (size === 'a4') {
      return 350;
    }
    return 250;
  };

  const itemsTotalPrice = () => {
    if (itemsInCart !== undefined) {
      const totalPrice = Object.keys(itemsInCart).reduce((previous, key) => {
        return (
          previous + itemsInCart[key].amount * countPrice(itemsInCart[key].size)
        );
      }, 0);

      return totalPrice;
    }
  };

  const renderImage = (title) => {
    console.log(title);
    const cartItem = blogs.filter((blog) => title === blog.title);
    console.log(cartItem);
    return cartItem[0].image;
  };
  return (
    <div className='cart'>
      <Link to='/cv'>
        <div className='portrait'></div>
      </Link>
      <div class='center'>
        <h2>your bag</h2>

        {itemsInCart.length === 0 && <div class='blogs'>No items here.</div>}
        <div className='flex'>
          {itemsInCart?.map((item, index) => (
            <div className='cart-item'>
              <div className='image-holder'>
                <img src={renderImage(item.title)} />
              </div>
              <p>
                {item.title}
                <span className='uppercase pink'> | {item.size}</span>
              </p>
              <p>
                limited edition of <b>12</b>
              </p>

              <p className='blue bolded'>
                {countPrice(item.size) * item.amount} dkk
              </p>
              <button onClick={() => onCartItemRemove(item.title, item.size)}>
                x
              </button>
            </div>
          ))}
        </div>
        <h2>Total</h2>
        <p style={{ marginTop: 0 }}>{itemsTotalPrice()} dkk</p>

        <p className='blue'>{}</p>
        <button>Checkout</button>
        <Link to='/tagged/print'>
          <button>All Prints</button>
        </Link>
      </div>
      <Footer itemsInCart={itemsInCart} />
    </div>
  );
};
export default Cart;
