import React, { useState } from 'react';
import tagged from '../tagged.json';
import blogs from '../blogs.json';
import {
  Blog,
  BlogList,
  Cart,
  Cv,
  KeywordSearch,
  Logo,
  ShoppingCart,
  Tagged,
} from './';
import { Route, Switch } from 'react-router-dom';

const sizes = ['a3', 'a4', 'a5'];

function App() {
  //items in the cart
  const [itemsInCart, setItemsInCart] = useState([]);

  //sorting the blogs here
  blogs.sort(function (a, b) {
    if (a.date < b.date) return 1;
    if (a.date > b.date) return -1;
  });

  const addThisItem = (title, size) => {
    //check for item with same name and size in the array
    const itemIndex = itemsInCart.findIndex(
      (item) => item.title === title && item.size === size
    );

    // if not here, simply add it.
    if (itemIndex === -1) {
      const itemInCart = { title: title, size: size, amount: 1 };
      setItemsInCart([...itemsInCart, itemInCart]); //simple value

      /* 
      const itemInCart = {
        title: title,
        size: size,
        amount: itemsInCart[itemIndex].amount + 1,
      };
      const updatedArray = itemsInCart;
      itemsInCart[itemIndex] = itemInCart;
      setitemsInCart(itemsInCart);
      */
    }
  };

  const removeThisItem = (title, size) => {
    const filtered = itemsInCart.filter(
      (item) => item.title !== title && item.size !== size
    );
    setItemsInCart(filtered);
  };

  const removeOne = (title, size) => {
    const itemIndex = itemsInCart.findIndex(
      (item) => item.title === title && item.size === size
    );
    const amount = itemsInCart[itemIndex].amount;
    if (amount === 1) {
      removeThisItem(title, size);
    } else {
      const itemInCart = {
        title: title,
        size: size,
        amount: amount - 1,
      };
      const updatedArray = itemsInCart;
      updatedArray[itemIndex] = itemInCart;
      itemsInCart(updatedArray);
    }
  };
  return (
    <>
      <Logo />
      <KeywordSearch />
      <ShoppingCart itemsInCart={itemsInCart} />
      <Switch>
        <Route
          path='/'
          render={() => (
            <BlogList
              onItemAdd={addThisItem}
              itemsInCart={itemsInCart}
              sizes={sizes}
            />
          )}
          exact
        />
        <Route path='/cv' component={Cv} exact />
        <Route
          path='/cart'
          render={() => (
            <Cart
              itemsInCart={itemsInCart}
              onItemAdd={addThisItem}
              onCartItemRemove={removeThisItem}
            />
          )}
          exact
        />
        {blogs.map((blog, index) => (
          <Route
            path={`/:name`}
            component={Blog}
            sizes={sizes}
            key={index}
            exact
          />
        ))}
        {tagged.map((tag, index) => (
          <Route
            path={`/tagged/:name`}
            render={(props) => (
              <Tagged
                {...props}
                key={index}
                sizes={sizes}
                onItemAdd={addThisItem}
              />
            )}
            exact
          />
        ))}
      </Switch>
    </>
  );
}

export default App;
